$primary-color: #00A1CC;
$dark-gray: #737373;
$light-gray: #999999;
$white: #FFFFFF;

body {
    font-family: 'Roboto', sans-serif;
    background: $primary-color;
}

.container {
    width: 30%;
    margin: 2em auto;
    padding: 2em 0 0;
    h1 {
        font: {
            weight: 300;
            size: 3em;
        }
        text-align: center;
        color: $white;
    }
    h2 {
        font: {
            weight: 300;
            size: 2em;
        }
        text-align: center;
        color: #212121;
        margin: 0em 0 1em;
    }
}

#signinForm {
    width: 80%;
    height: 50%;
    margin: 0 auto;
    transition: 0.8s;
    background: $white;
    padding: 2em;
    border-radius: 5px;
}

.form-group {
    margin: 0 0 1em 0;
    color: $dark-gray;
    font-size: .9em;
    width: 100%;
}

.form-label {
    color: #FF0000;
    display: inline-block;
    width: 4%;
}

.form-style {
    outline: none;
    font-size: .9em;
    color: $dark-gray;
    padding: 1em;
    width: 84%;
    border: solid 1px #e2eaef;
    border-radius: 5px;
    &::placeholder {
        color: #CCCCCC;
    }
}

.form-radio {
    margin: 0px 3px 0px 5px;
}

.error-message {
    display: block;
    color: #FF0000;
    margin: .3em 0 0 1.5em;
}

.form-submit {
    font-size: 1em;
    color: $white;
    background: $primary-color;
    border: 3px solid $primary-color;
    outline: none;
    cursor: pointer;
    margin-top: 2em;
    padding: .6em 1em;
    width: 100%;
    border-radius: 5px;
    transition: 0.5s all;
}

.form-submit {
    &:hover {
    background: #009FFB;
    border: 3px solid #009FFB;
    }
    &:disabled {
        background: $light-gray;
        border: 3px solid $light-gray;
        color: $white;
    }
}

.copyright {
    text-align: center;
    margin: 2em 0 0;
    font-size: .9em;
    color: $white;
}

/* Responsive Web Design */
@media(max-width:1440px) {
    .container {
        width: 33%;
    }
}

@media(max-width:1024px) {
    .container {
        width: 47%;
    }
}

@media(max-width:768px) {
    .container {
        width: 60%;
        margin: 1em auto;
        h1 {
            font-size: 2.5em;
        }
    }
}

@media(max-width: 600px) {
    .container {
        padding: 0;
        width: 80%;
        h1 {
            font-size: 2.2em;
        }
    }
    .form-style {
        font-size: 0.85em;
        width: 80%;
    }
}

@media(max-width:320px) {
    .container {
        width: 90%;
        height: 470px;
        h1 {
            font-size: 1.6em;
        }
        h2 {
            font-size: 1.4em;
        }
    }
    #signinForm {
        padding: 0.7em 1.5em;
        height: 63%;
    }
    .copyright {
        margin: 2em 0;
    }
}